.header {
  padding: 0.9375rem 0.625rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--veryDarkBlue-TopBGPattern);
}
@media (min-width: 40em) {
  .header {
    flex-direction: row-reverse;
  }
}
.header__image img {
  max-width: 100%;
  height: auto;
  border-radius: 0.5rem;
}
@media (min-width: 40em) {
  .header__image img {
    margin-left: 0.9375rem;
  }
}
.header__descript {
  padding-left: 0.625rem;
  text-align: start;
}
@media (min-width: 71.875em) {
  .header__descript {
    padding-left: 0;
    text-align: right;
  }
}
.header__descript h1 {
  font-size: 1.25rem;
  color: var(--lightGray);
}
.header__descript span {
  font-size: 1rem;
  color: var(--grayishWhite);
}

@media (min-width: 40em) {
  .main {
    margin: 1.5625rem;
    width: 90%;
    margin: auto;
  }
}
.main .card {
  margin: 0.625rem;
}
.main .card__about--aboutMe h2 {
  margin-bottom: 0.9375rem;
  color: var(--lightGray);
}
.main .card__about--aboutMe p {
  font-size: 1rem;
  line-height: 1.6;
  color: var(--grayishWhite);
  margin-bottom: 0.625rem;
}
.main .card__about--aboutMe p a {
  color: var(--grayishWhite);
}
.main .card__about--aboutMe p a:hover {
  color: blue;
}
.main .card__skills {
  width: 100%;
  margin-block: 1.25rem;
}
.main .card__skills h2 {
  margin-bottom: 0.9375rem;
  color: var(--lightGray);
  font-size: 1.25rem;
}
.main .card__skills__grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
@media (min-width: 40em) {
  .main .card__skills__grid {
    justify-content: start;
    flex-direction: row;
  }
}
.main .card__skills__grid .skill-card {
  width: 13.125rem;
  background-color: var(--purplishBlue);
  color: var(--grayishWhite);
  padding: 1rem;
  border-radius: 0.625rem;
  margin: 0.9375rem;
}
@media (min-width: 40em) {
  .main .card__skills__grid .skill-card {
    margin: 0.3125rem;
  }
}
.main .card__skills__grid .skill-card .icon {
  max-width: 2.5625rem;
  height: 2.5625rem;
}
.main .card__skills__grid .skill-card .title {
  color: var(--veryDarkBlue-TopBGPattern);
  margin-block: 0.5rem;
}
.main .card__skills__grid .skill-card .level {
  font-size: 0.875rem;
}
.main .card__projects h3 {
  margin-block: 0.9375rem;
  color: var(--lightGray);
  font-size: 1.25rem;
}
.main .card__projects--projects {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media (min-width: 40em) {
  .main .card__projects--projects {
    align-items: center;
    border-radius: 0.625rem;
    padding: 0.9375rem;
    box-shadow: 0px 5px 20px 0px rgba(255, 255, 255, 0.127);
    background-color: var(--darkDesaturatedBlue-CardBG);
  }
}
.main .card__projects--projects .container {
  margin-bottom: 0.9375rem;
  flex: 50%;
  max-width: 50%;
  padding: 0 0.5rem;
}
@media (min-width: 40em) {
  .main .card__projects--projects .container {
    position: relative;
    flex: 33.33%;
    max-width: 33.33%;
    margin-bottom: 0;
    padding-right: 0.5rem;
    transition: transform 0.3s ease;
  }
  .main .card__projects--projects .container:hover {
    z-index: 1;
    transform: scale(1.08);
  }
}
.main .card__projects--projects .container a {
  text-decoration: none;
}
.main .card__projects--projects .container a img {
  max-width: 100%;
  height: auto;
  max-height: 15.625rem;
  margin-top: 0.5rem;
  box-shadow: 0px -10px 20px -5px #000000;
  border-top-left-radius: 0.3125rem;
  border-top-right-radius: 0.3125rem;
}
@media (min-width: 40em) {
  .main .card__projects--projects .container a img {
    border-radius: 0.3125rem;
    box-shadow: none;
    flex-shrink: 0;
    transition: all 0.3s ease;
  }
  .main .card__projects--projects .container a img:hover {
    box-shadow: 0px 5px 20px 0px #000000;
  }
}
@media (min-width: 40em) {
  .main .card__projects--projects .container a:hover + .descript {
    opacity: 1;
  }
}
.main .card__projects--projects .container .descript {
  padding: 0.3125rem;
  margin-top: -0.25rem;
  text-align: center;
  font-size: 0.875rem;
  border-bottom-left-radius: 0.3125rem;
  border-bottom-right-radius: 0.3125rem;
  background-color: var(--darkPurple);
  color: var(--white-Text);
  z-index: 1;
  box-shadow: 0px 0px 20px 2px #000000;
}
@media (min-width: 40em) {
  .main .card__projects--projects .container .descript {
    position: absolute;
    bottom: 4px;
    width: calc(100% - 16px);
    height: 40px;
    font-size: 1rem;
    padding: 0;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    transition: opacity 0.3s ease;
    pointer-events: none;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: none;
  }
}

.footer {
  position: relative;
  padding: 0.9375rem 0.625rem;
  background-color: var(--veryDarkBlue-TopBGPattern);
}
@media (min-width: 40em) {
  .footer {
    height: 160px;
  }
}
.footer__goto {
  margin-left: 0.9375rem;
}
@media (min-width: 40em) {
  .footer__goto {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
.footer__goto ul {
  color: var(--white-Text);
}
.footer__goto ul li {
  margin-top: 0.625rem;
  margin-bottom: 0.9375rem;
}
.footer__goto ul li a {
  color: var(--white-Text);
  text-decoration: none;
  font-size: 1.125rem;
}
.footer__social {
  position: absolute;
  top: 0;
  right: 0;
  margin-top: 0.9375rem;
  padding: 0.625rem;
  text-align: end;
}
@media (min-width: 40em) {
  .footer__social {
    margin-top: 1.5625rem;
  }
}
.footer__social .links {
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer__social .links a {
  text-decoration: none;
  color: var(--white-Text);
  margin-inline: 0.3125rem;
}
.footer__social .links a span {
  font-size: 1.25rem;
  transition: all 0.2s ease;
}
.footer__social .links a .bi-github {
  color: var(--githubIcon);
}
@media (min-width: 40em) {
  .footer__social .links a .bi-github:hover {
    color: var(--githubIcon-hover);
  }
}
.footer__social .links a .bi-linkedin {
  color: var(--linkedinIcon);
}
@media (min-width: 40em) {
  .footer__social .links a .bi-linkedin:hover {
    color: var(--linkedinIcon-hover);
  }
}
.footer__social .links a img {
  vertical-align: middle;
}
.footer__copy {
  position: absolute;
  bottom: 0;
  right: 0;
  padding-bottom: 0.625rem;
  padding-right: 0.625rem;
  color: var(--desaturatedBlue-Text);
  font-size: 0.875rem;
}

:root {
  --font-Inter: "Inter", sans-serif;
}

:root {
  --veryDarkBlue-BG: hsl(213, 73%, 15%);
  --veryDarkBlue-TopBGPattern: hsl(240, 28%, 14%);
  --darkDesaturatedBlue-CardBG: rgba(225, 225, 225, 0.2);
  --desaturatedBlue-Text: hsl(228, 34%, 66%);
  --white-Text: hsl(0, 0%, 100%);
  --lightGray: hsl(164, 45%, 94%);
  --grayishWhite: hsl(0, 0%, 85%);
  --purplishBlue: hsl(250, 94%, 66%);
  /* Icons */
  --githubIcon: hsl(0, 0%, 96%);
  --githubIcon-hover: hsl(0, 0%, 76%);
  --linkedinIcon: hsl(196, 100%, 43%);
  --linkedinIcon-hover: hsl(196, 100%, 33%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-Inter);
  background-color: var(--veryDarkBlue-BG);
}